<template>
  <div class="app-container">
    <!-- Top Navigation Bar -->
    <header class="navbar">
      <nav>
        <ul>
          <li>
            <a href="#" :class="{ active: currentPage === 'home' }" @click="currentPage = 'home'">
              Home
            </a>
          </li>
          <li>
            <a href="#" :class="{ active: currentPage === 'fitness-planner' }" @click="currentPage = 'fitness-planner'">
              Fitness and Diet Planner
            </a>
          </li>
          <li>
            <a href="#" :class="{ active: currentPage === 'community' }" @click="currentPage = 'community'">
              Community
            </a>
          </li>
          <li>
            <a href="#" :class="{ active: currentPage === 'contact' }" @click="currentPage = 'contact'">
              Contact Me
            </a>
          </li>
        </ul>
      </nav>
    </header>

    <!-- Page Content -->
    <div class="page-container">
      <!-- Home Page -->
      <div v-if="currentPage === 'home'">
        <h1>Welcome to the Home Page</h1>
        <p>This is the home page content.</p>
      </div>

      <!-- Fitness and Diet Planner -->
      <div v-if="currentPage === 'fitness-planner'">
        <div class="chat-container">
          <h1>Fitness and Diet Planner</h1>
          <div class="chat-box">
            <div
              v-for="(msg, index) in messages"
              :key="index"
              :class="['message', msg.role === 'user' ? 'user' : 'assistant']"
            >
              <p v-if="msg.role === 'user'"><strong>{{ msg.role }}:</strong> {{ msg.content }}</p>
              <div v-else v-html="msg.content"></div>
            </div>
          </div>
          <div class="input-container">
            <input
              v-model="userMessage"
              placeholder="Type your message"
              @keyup.enter="sendMessage"
              class="chat-input"
            />
            <button @click="sendMessage" class="send-button">Send</button>
          </div>
        </div>
      </div>

      <!-- Community Page -->
      <div v-if="currentPage === 'community'">
        <h1>Community</h1>
        <p>Welcome to the community page, share your thoughts and experiences.</p>
      </div>

      <!-- Contact Page -->
      <div v-if="currentPage === 'contact'">
        <h1>Contact Me</h1>
        <p>Reach out to us through the following ways.</p>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      currentPage: "home", // Default page is Home
      userMessage: "",
      messages: [], // Stores all chat messages
    };
  },
  methods: {
    async sendMessage() {
      if (!this.userMessage) return;

      // Add user's message to the chat log
      const userMsg = { role: "user", content: this.userMessage };
      this.messages.push(userMsg);

      try {
        // Send user's message to the backend
        const response = await axios.post(
          "http://localhost:3231/api/chat",
          {
            userMessage: this.userMessage,
          },
          {
            headers: {
              "Content-Type": "application/json",
            }
          }
        );

        // Process AI response to make it structured
        const structuredContent = this.formatResponse(response.data);

        // Add AI's response to the chat log
        this.messages.push({ role: "assistant", content: structuredContent });
        this.userMessage = "";
      } catch (error) {
        console.error("Error:", error);
      }
    },
    formatResponse(response) {
      // Split the response into points with headings and body text
      const lines = response.split("\n").filter((line) => line.trim() !== "");

      return lines
        .map((line, index) => {
          if (line.startsWith("1.") || line.startsWith("2.") || line.startsWith("3.") || /^[0-9]+\./.test(line)) {
            return `<h3 class="response-heading">${line.trim()}</h3>`;
          } else {
            return `<p class="response-body">${line.trim()}</p>`;
          }
        })
        .join("");
    },
  },
};
</script>

